import React from 'react'
import DSButton from '@elliemae/ds-basic/Button'
import { ShuttleListPanel as Wrapper } from '../classedComponents'

function ShuttleListPanel({
  onClick = () => null,
  open = false,
  children,
}) {
  return (
    <Wrapper classProps={{ open }}>
      <DSButton
        buttonType='text'
        icon={children}
        onClick={onClick}
      />
    </Wrapper>
  )
}

export default ShuttleListPanel
