import React, { useContext } from 'react'
import { useTransition, animated } from '@elliemae/ds-shared/react-spring'
import { animationConfig } from '../animation/animationConfig'
import AnimationContext from '../AnimationState'

function OverflowList({
  className = '',
  activeHierarchy = '',
  children,
}) {
  const { state: { isDrillingDown = false }, setIsDrillingDown } = useContext(AnimationContext.Context)

  const overflowTransition = useTransition(activeHierarchy, null, {
    ...animationConfig.moveList(isDrillingDown),
    onDestroyed: () => setIsDrillingDown(false),
  })
  return overflowTransition.map(({ props, key }) => (
    <animated.div
      key={key}
      className={className}
      style={props}
    >
      {children}
    </animated.div>
  ))
}

export default OverflowList
