/* eslint-disable react/no-unused-state */
import React from 'react';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import DSIndeterminateProgressIndicator from '@elliemae/ds-basic/IndeterminateProgressIndicator';

const blockName = 'shuttle-infinite-scroll-indicator';

const InfiniteScrollBarContainer = aggregatedClasses('div')(
  blockName,
  null,
  ({ isOpen }) => ({ opened: isOpen }),
);

const ShuttleInfiniteScrollIndicator = ({ isOpen }) => {
  return (
    <InfiniteScrollBarContainer classProps={{ isOpen }} data-testid={blockName}>
      <DSIndeterminateProgressIndicator processing title="Loading" />
    </InfiniteScrollBarContainer>
  );
};

export default ShuttleInfiniteScrollIndicator;
