/* eslint-disable react/no-unused-state */
import React, { Component } from 'react'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import { debounce } from '@elliemae/ds-utilities/utils'
import DSTextBox from "@elliemae/ds-basic/form/TextBox"

const blockName = 'shuttle-search-box'

const SearchBoxContainer = aggregatedClasses('div')(blockName, null, ({ isOpen }) => ({ opened: isOpen }))

const filterList = debounce((term, onFilterCallback) => {
  onFilterCallback(term)
}, 200)

export default class ShuttleSearchBox extends Component {
  static defaultProps = {
    filterOnKeyStroke: false,
    onChange: () => null,
    onFilter: () => null,
  }
  constructor(props) {
    super(props)
    this.state = {
      value: props.value,
      prevProps: {},
    }
  }
  static getDerivedStateFromProps(nextProps, { prevProps }) {
    const { value } = nextProps
    if (value !== prevProps.value) {
      return {
        value,
        prevProps: { value },
      }
    }
    return null
  }
  handleChange(e) {
    const { onChange, filterOnKeyStroke, onFilter } = this.props
    const { value } = e.target
    onChange(value)
    if (filterOnKeyStroke) filterList(value, onFilter)
    this.setState({ value })
  }
  handleKeyPress(e) {
    const { onFilter } = this.props
    if (e.keyCode === 13) onFilter(e.target.value)
  }
  render() {
    const { value } = this.state
    const {
      isOpen,
      onClose,
      placeholder,
    } = this.props
    return (
      <SearchBoxContainer classProps={{ isOpen }}>
        <DSTextBox
          autoFocus
          clearable
          onChange={this.handleChange}
          onClear={onClose}
          onKeyDown={this.handleKeyPress}
          placeholder={placeholder}
          value={value}
        />
      </SearchBoxContainer>
    )
  }
}
