import { memo } from 'react'
import { areEqual } from 'react-window'

const VirtualizedItem = memo(({
  data,
  index,
  style: virtualizedStyles,
  showSortHandler = false,
}) => {
  const {
    items,
    itemRenderer,
    isItemMoving,
  } = data
  const {
    item,
    props: animatedStyles,
    key,
  } = items[index] || {}
  if (!item) return null
  return (itemRenderer({
    index,
    key,
    item,
    showSortHandler,
    style: isItemMoving ? { ...animatedStyles, ...virtualizedStyles } : virtualizedStyles,
  }))
}, areEqual)

export default VirtualizedItem
